.header-menu__button {
    position: relative;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-between;
    width: 24px;
    height: 16px;
    border: none;
    background-color: transparent;
}

.header-menu__button>span {
    height: 2px;
    border-radius: 999px;
    background-color: #383838;
    transition: all 0.5s ease 0s;
}

.header-menu__button>span:nth-child(1) {
    width: 75%;
}

.header-menu__button>span:nth-child(2) {
    width: 100%;
}

.header-menu__button>span:nth-child(3) {
    width: 50%;
}

.header-menu__button.--active>span {
    position: absolute;
    top: 50%;
}

.header-menu__button.--active>span:nth-child(1) {
    width: 100%;
    transform: translateY(-50%) rotate(45deg);
}

.header-menu__button.--active>span:nth-child(2) {
    transform: scale(0);
}

.header-menu__button.--active>span:nth-child(3) {
    width: 100%;
    transform: translateY(-50%) rotate(-45deg);
}

@media only screen and (min-width: 768px) {
    .header-menu__button {
        width: 32px;
        height: 22px;
    }
}

@media only screen and (min-width: 1280px) {
    .header-menu__button {
        display: none;
    }
}

.header-menu__body {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-height: calc(100vh - 100%);
    padding: 24px 15px 24px 15px;
    overflow: auto;
    background-color: #ffffff;
    z-index: -2;
    border-bottom: 1px solid rgba(56, 56, 56, 0.25);
}

@media only screen and (min-width: 1280px) {
    .header-menu__body {
        position: static;
        padding: 0 0 0 0;
        border-bottom: none;
        background-color: transparent;
        transition: none !important;
        overflow: auto;
        z-index: 0;
    }
}

.header-menu__body-footer {
