@media only screen and (min-width: 1280px) {

    .header-menu__item:hover>.header-menu__submenu,
    .header-menu__item:focus-within>.header-menu__submenu {
        opacity: 1;
        transform: translateY(100%);
        transition: all 0.5s ease 0s;
        z-index: -2;
    }
}

.header-menu__link {
    display: flex;
